.li_bg {
  width: 150px;
  height: 175px;
  margin: 0 auto;
  background-size: 100% 100%;
  -webkit-transform: rotateZ(0);
          transform: rotateZ(0);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: all .3s ease-out;
}
.li_bg.active {
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}
.li_title {
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  margin-bottom: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-180%);
          transform: translate(-50%,-180%)
}
.li_img {
  display: block;
  width: 80px;
  height: 65px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: all .3s ease-out;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-20%);
          transform: translate(-50%,-20%)
}
.li_img.act {
  -webkit-transform: translate(-30%,5%) rotate(-45deg);
          transform: translate(-30%,5%) rotate(-45deg);
  width: 120px;
  height: 100px;
}

